import React, {FC, useEffect, useMemo, useState} from 'react';
import './Game.scss';
import Board from "../Board/Board";
import {message} from 'antd';

interface IProps {
    winPos: number[][]
}

interface ISetAlert {
    (state: 'win' | 'draw', winner: 'крестики' | 'нолики' | null): void
}

interface IGame {
    isGameEnd: boolean,
    step: number,
    crossWins: number,
    zeroWins: number,
    drawCount: number
}

const Game: FC<IProps> = ({winPos}) => {
    const getLS = (item: string): number => Number(localStorage.getItem(item))
    const setLS = (name: string, item: string): void => localStorage.setItem(name, item)

    const [game, setGame] = useState<IGame>({
        step: 0, isGameEnd: false, crossWins: getLS('crossWins'),
        zeroWins: getLS('zeroWins'), drawCount: getLS('drawCount')
    })
    const isZero = useMemo(() => game.step % 2 === 0 ? 'x' : 'o', [game.step])

    const handleCellClick = ({target}: any): void => {
        if (game.isGameEnd || target.innerHTML === 'x' || target.innerHTML === 'o') return

        const cellsEl = document.querySelectorAll('.game-board__cell')
        target.innerHTML = isZero
        setGame({...game, step: game.step + 1})
        let stepForDraw = 0

        winPos.forEach((item: number[], i: number) => {
            const zeroWin: boolean = cellsEl[winPos[i][0]].innerHTML === 'o' &&
                cellsEl[winPos[i][1]].innerHTML === 'o' &&
                cellsEl[winPos[i][2]].innerHTML === 'o'

            const crossWin: boolean = cellsEl[winPos[i][0]].innerHTML === 'x' &&
                cellsEl[winPos[i][1]].innerHTML === 'x' &&
                cellsEl[winPos[i][2]].innerHTML === 'x'

            if (crossWin) {
                setAlert("win", "крестики")
                handleCellClick({target})
            } else if (zeroWin) {
                setAlert("win", "нолики")
                handleCellClick({target})
            } else if (game.step === 8 && !crossWin && !zeroWin) ++stepForDraw
        })
        if (stepForDraw === 8) setAlert('draw', null)
    }

    const setAlert: ISetAlert = (state, winner) => {
        if (state === 'win' && winner) {
            const zeroWin = winner === 'нолики'
            const crossWins: number = zeroWin ? game.crossWins : game.crossWins + 1;
            const zeroWins: number = zeroWin ? game.zeroWins + 1 : game.zeroWins;

            setGame({...game, isGameEnd: true, zeroWins, crossWins})
            setLS(zeroWin ? 'zeroWins' : 'crossWins', `${zeroWin ? zeroWins : crossWins}`)
            message.success(`${winner} победили`)

        } else if (state === 'draw') {
            const drawCount: number = game.drawCount + 1
            setGame({...game, isGameEnd: true, drawCount})
            setLS('drawCount', `${drawCount}`)
            message.warning('ничья')
        }
    }

    const initCells = (): void => {
        const cellsEl = document.querySelectorAll('.game-board__cell')
        cellsEl.forEach((item) => item.innerHTML = '')
        setGame({...game, step: 0, isGameEnd: false})
        message.info('игра начинается 😈')
    }

    useEffect(() => initCells(), [])

    const resetWins = (): void => {
        setGame({...game, crossWins: 0, zeroWins: 0, drawCount: 0})
        setLS('crossWins', '0')
        setLS('zeroWins', '0')
        setLS('drawCount', '0')
    }

    return (
        <div className="game">
            <div className='game-step'>Ходят – {isZero}</div>
            <div className="game-score">
                Нолики: {game.zeroWins} <br/>
                Крестики: {game.crossWins} <br/>
                Ничьи: {game.drawCount} <br/>
                <button className="game__reset" onClick={resetWins}>Сбросить</button>
            </div>
            <Board handleCellClick={handleCellClick}/>
            <hr className='game__divider'/>
            <button className="game__reset" onClick={() => initCells()}>Заново</button>
        </div>
    );
};

export default Game;