import {AnimatePresence, motion} from 'framer-motion';
import React, {FC} from 'react';
import './Board.scss';

interface IProps {
    handleCellClick: ({target}: any) => void
}

const Board: FC<IProps> = ({handleCellClick}) => {
    const animBoard = {
        initial: {opacity: 0},
        animate: {opacity: 1, transition: {duration: 0.2, staggerChildren: 0.2}},
    }

    const animItem = {
        initial: {y: -15, opacity: 0, scale: 0},
        animate: {y: 0, opacity: 1, scale: 1},
    }

    const allCells: number[] = [0, 1, 2, 3, 4, 5, 6, 7, 8]

    return (
        <motion.div variants={animBoard} animate='animate' initial='initial' className='game-board'>
            <AnimatePresence>
                {allCells && allCells.map((item: number) =>
                    <motion.div variants={animItem} onClick={({target}: any) => handleCellClick({target})}
                                key={item} className='game-board__cell'/>
                )}
            </AnimatePresence>
        </motion.div>
    );
};

export default Board;